	module.exports = {"data":{"markdownRemark":{"html":"<p>A watercolor painting the cement factory on the back.</p>\n<p>In the class of art, the work which I drew with usual Nori is a work that has quite outstanding results such as the <em>school exhibition silver prize + the district exhibition silver prize</em> somehow. By the way, this was the first and last time that I won the prize as a work of art (excluding fan illustration).</p>\n<p><a href=\"https://www.google.co.jp/maps/@35.940711,139.337592,3a,75y,34.79h,95.36t/data=!3m4!1e1!3m2!1s3TfJl9DgCF2KcOAbpBQcKA!2e0\">The place of sketching is probably around here</a> . The cement factory that was supposed to be in the back has already been dismantled and it has become the current material storage site, which is quite different from the current landscape.</p>","frontmatter":{"cover":null,"date":"1992-10-12T14:00:06+09:00","strDate":"1992/10/12","title":"\"Autumn landscape\""}}},"pathContext":{"path":"/en/blog/1992/10/12-autumn/","langKey":"en"}}

